import React from 'react';
import axios from 'axios';
import Spinner from './components/Spinner';

import { Header } from "./components/Header";
import { Details, Home, MoviePlayer, NotFound } from './routes';
import { API_KEY, API_URL, IMAGE_BASE_URL, BACKDROP_SIZE } from './config';
import { BrowserRouter, Route, Switch} from 'react-router-dom';

import './App.css';
import './css/Header.css';
import { Provider } from 'react-redux';
import store from './store';


class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      movies: [
          {
            backdrop_path: "/n6bUvigpRFqSwmPp1m2YADdbRBc.jpg",
            id: 475557,
            overview:
              "Dans les années 1980, à Gotham City, Arthur Fleck, un humoriste de stand-up raté, bascule dans la folie et devient le Joker.",
            poster_path: "/tWjJ3ILjsbTwKgXxEv48QAbYZ19.jpg",
            title: "Joker"
          },
          {
            backdrop_path: "/n6bUvigpRFqSwmPp1m2YADdbRBc.jpg",
            id: 475558,
            overview:
              "Dans les années 1980, à Gotham City, Arthur Fleck, un humoriste de stand-up raté, bascule dans la folie et devient le Joker.",
            poster_path: "/tWjJ3ILjsbTwKgXxEv48QAbYZ19.jpg",
            title: "Joker"
          },
          {
            backdrop_path: "/n6bUvigpRFqSwmPp1m2YADdbRBc.jpg",
            id: 475559,
            overview:
              "Dans les années 1980, à Gotham City, Arthur Fleck, un humoriste de stand-up raté, bascule dans la folie et devient le Joker.",
            poster_path: "/tWjJ3ILjsbTwKgXxEv48QAbYZ19.jpg",
            title: "Joker"
          },
      ],
      badge: 0,
      image: './images/Fast_large.jpg',
      mTitle: "Fast And Furious",
      mDesc: 'Id nostrud quis officia magna esse esse ea.',
      activePage: 0,
      totalPages: 0,
      searchText: ""
    }
  }

  async componentDidMount() {
    try {
      const { data: {results, page, total_pages}} = await this.loadMovies();
      this.setState({
        movies: results,
        activePage: page,
        totalPages: total_pages,
        image: `${IMAGE_BASE_URL}/${BACKDROP_SIZE}/${results[0].backdrop_path}`,
        mTitle: `${results[0].title}`,
        mDesc: `${results[0].overview}`,
        mId: `${results[0].id}`,
        loading: false,
      })
    } catch(e) {
        console.error(e);
    }

  }

  loadMovies = () => {
    const page = this.state.activePage + 1;
    const url = `${API_URL}/movie/popular?api_key=${API_KEY}&page=${page}&language=fr`;
    return axios.get(url);
  }

  searchMovie = () => {
    const url = `${API_URL}/search/movie?api_key=${API_KEY}&query=${this.state.searchText}&language=fr`;
    return axios.get(url);
  }
  searchClickHandle = (searched) => {
      try {
        this.setState({loading: true, searchText: searched, image: null}, async () => {
          const { data: {results, page, total_pages}} = await this.searchMovie();
          this.setState({
            movies: results,
            activePage: page,
            totalPages: total_pages,
            image: `${IMAGE_BASE_URL}/${BACKDROP_SIZE}/${results[0].backdrop_path}`,
            mTitle: `${results[0].title}`,
            mDesc: `${results[0].overview}`,
            loading: false,
          })
        })
        
      } catch(e) {
          console.error(e);
      }
  }

  loadMoreHandle = async () => {
      try {
        this.setState({loading: true})
        const { data: {results, page, total_pages}} = await this.loadMovies();
        this.setState({
          movies: [...this.state.movies, ...results],
          activePage: page,
          totalPages: total_pages,
          image: `${IMAGE_BASE_URL}/${BACKDROP_SIZE}/${results[0].backdrop_path}`,
          mTitle: `${results[0].title}`,
          mDesc: `${results[0].overview}`,
          loading: false,
        })
      } catch(e) {
          console.error(e);
      }
  }

  render() {
    return (
      <Provider store={store}>
        <BrowserRouter>
          <div className="App">
            <Header badge={this.state.badge} {...this.state}/>
            {
              !this.state.image ? 
              (
                <Spinner/>
              ) :
              (
              <Switch>
                <Route exact path="/" render={ () => 
                  ( 
                    <Home {...this.state} onSearchClick={this.searchClickHandle} onButtonClick={this.loadMoreHandle}/>
                  )}/>
                  <Route exact path="/player" component={MoviePlayer}/>
                  <Route exact path="/player/:id" component={MoviePlayer}/>
                  <Route exact path="/:id" component={Details}/>
                  <Route render={() => <NotFound/>}/>
              </Switch>
              )
            }
          </div>
        </BrowserRouter>
      </Provider>
    );
  }
}

export default App;
