import React from 'react';
import FontAwesome from 'react-fontawesome';
import { Link } from 'react-router-dom';
import { POSTER_SIZE } from '../config';
import { addMovie, removeMovie } from '../actions/movie';
import {connect} from 'react-redux';

import '../css/Poster.css';

class PosterComponent extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            hover: false
        };
    }

    showOverlay = () => {
        if (this.state.hover) {
            return ;
        }
        this.setState({hover: true})
    }
    hideOverlay = () => {
        this.setState({hover: false})
    }

    add = () => {
        this.props.addMovie(this.props.movie)
    }

    remove = () => {
        this.props.removeMovie(this.props.movie.id)
    }

    render() {
        const URL_BASE_IMG = `http://image.tmdb.org/t/p/${POSTER_SIZE}/`;
        return (
            <div 
                onMouseEnter={this.showOverlay} 
                onMouseLeave={this.hideOverlay}
                class="movie-card">
                    <Link to={{pathname: `/${this.props.movie.id}`}}>
                        <img src={URL_BASE_IMG + this.props.imgSrc} alt="poster" className="poster--img"/>
                    </Link>
                    {this.state.hover ? 
                        ( 
                            <div class="overlay">
                                <h3>{this.props.mTitle}</h3>
                                {this.props.wished ? 
                                    (
                                        <FontAwesome onClick={this.remove} className="poster--icon" name="heart" size="3x"/>
                                    ) :
                                    (
                                        <FontAwesome onClick={this.add} className="poster--icon__not" name="heart-o" size="3x"/>
                                    )
                                }
                            </div>
                        ): 
                        null
                    }
            </div>
        );
    }
}

const mapDispatchToProps = dispatch => {
    return {
        addMovie: movie => dispatch(addMovie(movie)),
        removeMovie: movieID => dispatch(removeMovie(movieID)),
    }   
}
const Poster = connect(null,mapDispatchToProps)(PosterComponent);

export {Poster};