import axios from 'axios';
import React from 'react';
import { ActorList, HeaderDetails } from '../components';
import { API_KEY, API_URL, BACKDROP_SIZE, IMAGE_BASE_URL } from '../config';
import '../css/Detail.css';

import Spinner from '../components/Spinner';

class Details extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: true,
            actors: [
                {name: "Rick Hochet"},
                {name: "Maude Zarella"},
                {name: "John Doe"},
                {name: "Alain Proviste"},
            ],
            mTitle: 'Lorem',
            mDesc: 'Quis non exercitation incididunt esse do ut.',
            imgSrc: './images/Fast_large.jpg',
            revenue: '$123456789',
            runTime: '2h30',
            status: 'Released',
            vote: ''
        };
    }

    async componentDidMount() {
        try {
            const movieID = this.props.match.params.id;
            const url = `${API_URL}/movie/${movieID}?api_key=${API_KEY}&language=fr`;
            const {
                data: {
                    revenue, runtime, title, overview, status, vote_average, poster_path, backdrop_path, genres
                }
            } = await this.loadInfos(url);
            // la callback est appellé automatiquement apres que le state soit mis a jour
            this.setState({
                revenue,
                status,
                mTitle: title,
                mDesc: overview,
                runTime: runtime,
                vote: vote_average,
                imgSrc: poster_path,
                backdropPath: backdrop_path,
                genres: genres
            }, async () => {
                const url =  `${API_URL}/movie/${movieID}/credits?api_key=${API_KEY}&language=fr`;
                const {data: {cast}} = await this.loadInfos(url);
                this.setState({
                    actors: [...cast],
                    loading: false
                });
            });
        } catch (e) {
            console.error(e);
            this.setState({loading: false});
        }
    }

    loadInfos = (url) => axios.get(url);

    render() {
        const {loading, mTitle, mDesc, imgSrc, revenue, backdropPath, genres, runTime, status, vote } = this.state;
        const bgUrl = `${IMAGE_BASE_URL}/${BACKDROP_SIZE}/${backdropPath}`;
        return(
            <div className="movie-detail-container">
                {
                    loading ? 
                    ( <Spinner/> ) 
                    : 
                    (
                        <>

                            <div 
                                className="bg-image"                 
                                style={
                                    { backgroundImage: `url(${bgUrl})`}
                                }>
                            </div>
                            <HeaderDetails 
                                mTitle={mTitle} 
                                mDesc={mDesc} 
                                imgSrc={imgSrc}
                                backdropPath={backdropPath}
                                genres={genres}
                                runTime={runTime}
                                revenue={revenue}
                                status={status}
                                vote={vote}
                                />
                            <ActorList actors={this.state.actors}/>
                        </>
                    )
                }
            </div>
        );
    }
}
export {Details};