import React from 'react';
import FontAwesome from 'react-fontawesome';
import { Link } from 'react-router-dom';
import {connect} from 'react-redux';
import {getNumber} from '../actions/movie';
// import { HeaderImg } from './HeaderImg';


class HeaderComponent extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            showHeaderImg: true
        };
    }
    componentDidMount() {
        this.props.getNumber();
    }
    render() {
        return(
            <header>
            <div  className="header-banner">
                <h1>
                    <Link to="/" className="header-txt">Inflix</Link>
                </h1>
                <Link to="/player">
                    <FontAwesome className="header-bookmark" name="bookmark" size="3x"/>
                </Link>
                <div className="header-badge">{this.props.badge}</div>
            </div>
            </header>
        );
    }
}
const mapStateToProps = state => {
    return {
        badge: state.movies.number
    }
}
const mapDispatchToProps = dispatch => {
    return {
        getNumber: () => dispatch(getNumber())
    }
}
const Header = connect(mapStateToProps, mapDispatchToProps)(HeaderComponent);
export { Header };