import React from 'react';
import { Poster } from './index';
import '../css/PosterList.css';

class PosterList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
        };
    }
    renderPoster = () => {
        return this.props.movies.map(movie => {
            const imgSrc = movie.poster_path;
            let wish = false;
            if (this.props.localMovies) {
                this.props.localMovies.forEach(localMovie => {
                    if (localMovie.id === movie.id) {
                        wish = true;
                    }
                });
            }
            return (
                <Poster key={movie.id} imgSrc={imgSrc} wished={wish} movie={movie} mTitle={movie.title} mDesc={movie.overview} />
            );
        }) 
    }

    render() {
        return(
            <div>
                <h2 class="cards-title">Nouveaux films</h2>
                <div class="cards">
                    { this.renderPoster() }
                </div>
            </div>
        );
    }
}
export {PosterList};