import React from 'react';
import '../css/MvPlayerListItem.css';
import { Link } from 'react-router-dom';

class MvPlayerListItem extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
        };
    }
    render() {
        const activeClass = this.props.active ? 'list-item active' : 'list-item';
        return(
            <Link 
                style={{textDecoration: 'none', color: "white"}}
                to={{ pathname: `/player/${this.props.id}`}}>
                    <div class={activeClass}>
                        <p>{this.props.number}</p>
                        <p class="list-item-title">{this.props.title}</p>
                        <p>{this.props.duration}</p>
                    </div>
            </Link>
        );
    }
}
export {MvPlayerListItem};