import React from 'react';
import { MvPlayerListItem } from './MvPlayerListItem';
import '../css/MvPlayerList.css';


class MvPlayerList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
        };
    }
    renderList = props => {
        return props.movies.map((movie, i) => {
            const active = movie.id === props.selectedMovie.id ? true: false;
            return (
                <MvPlayerListItem
                    {...movie}
                    key={movie.id}
                    number={i+1}
                    active={active}
                />
            );
        });
    }
    render() {
        return(
            <div className="movies-list">
                <div className="movie-list-title">
                    <h2>Favoris: {this.props.selectedMovie.title}</h2>
                </div>
                <div className="list">
                    {this.renderList(this.props)}
                </div>
            </div>
        );
    }
}
export {MvPlayerList};