import React from 'react';
import '../css/Actor.css';

class Actor extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            hover: false
        };
    }

    showOverlay = () => {
        if (this.state.hover) {
            return ;
        }
        this.setState({hover: true})
    }
    hideOverlay = () => {
        this.setState({hover: false})
    }

    render() {
        const name = this.props.name.split(" ");
        return(
            <div 
                className="actor-card"
                onMouseEnter={this.showOverlay}
                onMouseLeave={this.hideOverlay}
            >
                {
                    this.props.imgSrc.includes('null') ?
                    <div className="no-name">
                            <p>{name}</p>
                    </div>
                    :
                    <img src={this.props.imgSrc} alt={'actor: ' + name}/>
                }
                {
                    this.state.hover &&  !this.props.imgSrc.includes('null')? 
                    ( 
                        <div class="overlay">
                            <p>{name}</p>
                        </div>
                    ): null
                }
            </div>
        );
    }
}
export {Actor};