import React from 'react';
import { Stars } from './Stars';
import { IMAGE_BASE_URL, POSTER_SIZE} from '../config';

import '../css/HeaderDetails.css';
import { calcTime } from '../utils/helpers';

class HeaderDetails extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
        };
    }

    calcVote = () => {
        this.fakeArray1 = [];
        this.fakeArray2 = [];
        const vote = Math.round(this.props.vote/2);
        const reste = 5 - vote;
        for(let i = 0; i < vote; i++) this.fakeArray1.push(i);
        if (reste > 0) {
            for(let i = 0; i < reste; i++) this.fakeArray2.push(i);
        }
    }

    renderGenres = () => {
        return this.props.genres.map(genre => {
            return (
                <li key={genre.id}>{genre.name}</li>
            );
        }) 
    }

    render() {
        this.calcVote();
        const imgSrc = `${IMAGE_BASE_URL}/${POSTER_SIZE}/${this.props.imgSrc}`;
        return (
                <div className="flex-container">
                <div className="movie-description">
                    <div className="card">
                        <div className="movie-img">
                            <img src={imgSrc} alt={this.props.mTitle} />
                        </div>
                        <div className="movie-detail">
                            <div className="movie-title">
                                <h2>{this.props.mTitle}</h2>
                                <p>
                                    <i className="far fa-clock"></i>
                                    {calcTime(this.props.runTime)}
                                </p>
                            </div>
                            <div className="movie-genre">
                                <p>Genre:</p>
                                <ul>
                                    { this.renderGenres() }
                                </ul>
                            </div>
                    
                            <div className="movie-overview">
                                <h2>Resumé:</h2>
                                <p>{this.props.mDesc}</p>
                            </div>
                            <div className="movie-stars">
                                <div className="stars">
                                    <p className="stars-txt">Note: </p>
                                    <Stars fakeArray1={this.fakeArray1} fakeArray2={this.fakeArray2}/>
                                </div>
                                <div className="actors">
                                    <a href="#actors">Voir les acteurs</a>
                                </div>
                            </div>

                            <div className="movies-links">
                                <p title="Bande d'annonce">
                                    <i className="far fa-play-circle"></i>
                                </p>
                                <p title="Ajouter au favoris">
                                    <i className="far fa-heart"></i>
                                </p>
                                <p title="Retour sur la page précedente">
                                    <i className="fas fa-arrow-left"></i>
                                </p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}
export {HeaderDetails};