import React from 'react';
import '../css/LoadButton.css';
import Spinner from './Spinner';


class LoadButton extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
        };
    }
    render() {
        return(
            <>
                {
                    this.props.loading ? 
                    (
                        <Spinner/>
                    )
                    :
                    (
                        <div className="load-btn" onClick={this.props.onButtonClick}>
                            <h3 className="load-btn-text">VOIR PLUS</h3>
                        </div>
                    )
                }
            </>
        );
    }
}
export {LoadButton};